@charset "UTF-8";
.productTile {
  display: flex;
  flex-direction: column;
  border-top: 6px solid #f28c14;
  width: 100%;
  padding: 20px;
  box-shadow: 0px 40px 20px -40px #858585;
  background: #ffffff; }
  .productTile .productInfo {
    text-align: center;
    font-size: 1.1rem;
    font-weight: 700; }
  .productTile span {
    display: block; }
  .productTile .cost {
    font-size: 1.6rem;
    padding-top: 0.8em; }
    .productTile .cost::before {
      font-family: Arial, Helvetica, sans-serif;
      content: '₹';
      display: inline-block;
      padding-right: 5px; }
  .productTile img {
    max-width: 100%;
    display: block;
    transform: scale(0.8);
    transition: all 0.5s ease;
    flex: 1; }
    .productTile img:hover {
      transform: scale(1); }
